.archivosContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 20px;
    max-width: 1400px;
    margin: 0 auto;
}
.archivosContainer::after {
    content: "";
    width: 300px;
}
.archivo{
    width: 100%;
    max-width: 300px;
    height: 180px;
    margin:0;
    border-radius: 5px 5px 0 0;
}

.archivoPlaceHolder{
    width: 300px;
    height: 180px;
    margin: 0;
    border-radius: 5px 5px 0 0;
}

.iframeBottom{
    display: flex;
    height: 70px;
    background-color: rgb(0,51,47);
    margin-top: -5px;
    border-radius: 0 0 5px 5px;
    max-width: 300px;
    justify-content: center;
    flex-wrap: wrap;
}
.displayTitle{
    width: 100%;
    position: relative;
    text-align: center;
    color: white;
    margin: 0;
    cursor: pointer;
    font-weight: bold;
}

.like{
    border-color: transparent;
    background-color: transparent;
}

.liked:hover{
    opacity: 0.4;
}

.toLike{
    opacity: 0.2;
}
.toLike:hover{
    opacity: 0.8;
}

.like:disabled{
    border-radius: 100%;
    background-color: rgba(187, 187, 187, 0.1);
}
.pagination{
    margin-bottom: 2rem;
    display: flex;
    flex-direction: row;
    justify-content: center;
    list-style-type: none;
    padding: 0 5rem;
}
.paginationPage{
    width: 2.5rem;
    cursor: pointer;
    border-right: 1px solid var(--blanco);
    user-select: none;
    text-align: center;
}


.pagination li a {
    padding: 0.1rem 0.9rem;
    color: white;
    min-width: 32px;
}

.fastMove{
    cursor: pointer;
    padding: 0.1rem 1rem;
    border-right: 1px solid var(--blanco);
    user-select: none;
}
.fastMove:last-of-type{
    border-right: none;
}

.activePage{
    background-color: var(--fondo-light);
}